.site-list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  flex-wrap: wrap;
}

.site-list a {
  display: block;
  padding: 19px;
  font-size: 14px;
  line-height: 22px;
  color: $white;
  text-transform: uppercase;
  text-decoration: none;
}

.site-list__item a {
  border-left: $bg-accent-color 1px solid;
}

.site-list__item:last-child a {
  border-right: $bg-accent-color 1px solid;
}

.site-list__item--active a {
  border: none;
  background-color: $bg-accent-color;
  color: $nav-active-text-color;
  padding-left: 24px;
  padding-right: 24px;
}

.site-list__item a[href]:hover {
  color: #000000;
  background-color: $white;
}

.site-list__item a[href]:active {
  color: rgba(0, 0, 0, 0.3);
  background-color: $white;
  opacity: 1;
}

@media screen and (max-width: $breakpoint-tablet) {
  .site-list {
    width: 100%;
    flex-direction: column;
  }
  .site-list__item a {
    font-size: 18px;
    line-height: 25px;
    padding-top: 18px;
    padding-bottom: 16px;
    border-top: $bg-accent-color 1px solid;
  }
  
  .site-list__item:last-child a {
    border-bottom: $bg-accent-color 1px solid;
  }
}
