html {
  font-size: 16px;
}

html,
body {
  min-height: 100%;
  min-width: 320px;
  position: relative;
}

body {
  background-color: #f0f0f0;
  font-size: 1rem;
  font-family: "OpenSans", monospace;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-wrapper {
  position: relative;
  width: 100%;
  max-width: 1170px;
  margin-left: auto;
  margin-right: auto;
}

.display-flex {
  display: flex;
  flex-wrap: wrap;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-footer, .main-header {
  background-color: #383838;
  background-image: url(../img/bg-wood-pattern.jpg);
  background-repeat: repeat;
  background-position: left top;
}

.aside__search__title,
.aside__topics__title,
.aside__reviews__title, .post__header, .features__title {
  color: #747474;
  text-transform: uppercase;
  font-family: "FjallaOne";
  font-size: 1.0625rem;
  font-weight: normal;
}

.main-header {
  width: 100%;
  height: 636px;
}

.main-header__tape {
  width: 100%;
  background-color: #383838;
  height: 60px;
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.43);
  margin-top: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-nav {
  font-family: "FjallaOne", Courier, monospace;
  font-weight: normal;
}

.main-header__wrapper {
  width: 1170px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header__logo {
  line-height: 0;
}

@media screen and (max-width: 768px) {
  .main-header {
    height: 906px;
  }

  .main-header__wrapper {
    flex-direction: column;
  }

  .main-nav {
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .main-header__tape {
    height: auto;
  }
}
.main-banner {
  background-color: #ffffff;
  box-shadow: 0px 1px 4px 0px rgba(82, 84, 77, 0.16);
  box-shadow: none;
  font-family: FjallaOne;
  color: rgba(255, 255, 255, 0.6);
  height: 447px;
  border: 20px solid #ffffff;
  width: 100%;
  border-bottom: none;
  overflow: hidden;
  position: absolute;
  top: -447px;
  border-radius: 6px 6px 0 0;
}

.main-banner__figure {
  margin: 0;
}

.main-banner__caption {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: -0.02em;
}

.main-banner__toggle {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 50%;
  transform: translateY(-78%);
}

.main-banner__next > svg,
.main-banner__prev > svg {
  height: 47px;
  width: 32px;
}

.main-banner__next > svg {
  transform: scaleX(-1) translateX(-1px);
}

.main-banner__text {
  width: 75%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 39.7%;
  left: 49.8%;
  transform: translate(-50%);
}

.main-banner__title {
  font-size: 4rem;
  font-weight: normal;
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
  text-align: center;
}

.main-banner__title--icon-user {
  position: relative;
}
.main-banner__title--icon-user::before {
  content: "\f007";
  position: absolute;
  top: -7rem;
  left: 50%;
  transform: translateX(-47%);
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: 6.1rem;
  color: inherit;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: rgba(255, 255, 255, 0.6);
}

.main-banner__description {
  font-size: 1.375rem;
  color: rgba(255, 255, 255, 0.6);
  letter-spacing: -0.02em;
  text-align: center;
  margin-top: 9px;
  margin-bottom: 17px;
  margin-left: 5px;
}

.main-banner__link {
  margin-left: 6px;
}

@media screen and (max-width: 768px) {
  .main-banner__title {
    font-size: 2.6rem;
  }
}
.site-list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  flex-wrap: wrap;
}

.site-list a {
  display: block;
  padding: 19px;
  font-size: 14px;
  line-height: 22px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}

.site-list__item a {
  border-left: #292929 1px solid;
}

.site-list__item:last-child a {
  border-right: #292929 1px solid;
}

.site-list__item--active a {
  border: none;
  background-color: #292929;
  color: #b297c4;
  padding-left: 24px;
  padding-right: 24px;
}

.site-list__item a[href]:hover {
  color: #000000;
  background-color: #ffffff;
}

.site-list__item a[href]:active {
  color: rgba(0, 0, 0, 0.3);
  background-color: #ffffff;
  opacity: 1;
}

@media screen and (max-width: 768px) {
  .site-list {
    width: 100%;
    flex-direction: column;
  }

  .site-list__item a {
    font-size: 18px;
    line-height: 25px;
    padding-top: 18px;
    padding-bottom: 16px;
    border-top: #292929 1px solid;
  }

  .site-list__item:last-child a {
    border-bottom: #292929 1px solid;
  }
}
.features {
  background-color: #ffffff;
  box-shadow: 0px 1px 4px 0px rgba(82, 84, 77, 0.16);
}

.features__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  min-height: 177px;
}

.features__item {
  flex-basis: 295px;
  flex-grow: 1;
  flex-shrink: 0;
  padding-bottom: 20px;
  text-align: center;
  background: linear-gradient(92.5deg, #e2e2e2 0%, #bebebe 1%, rgba(0, 0, 0, 0) 3%);
}
.features__item--first, .features__item--second, .features__item--third {
  padding-right: 29px;
}
@media screen and (max-width: 1170px) {
  .features__item {
    background: inherit;
  }
  .features__item--first, .features__item--second, .features__item--third {
    padding-right: 0;
    border-bottom: 1px solid rgba(190, 190, 190, 0.5);
  }
}

.features__item:first-child {
  background: #ffffff;
}

.features__item:last-child {
  flex-basis: 265px;
}

.features__title {
  margin-top: 100px;
  margin-bottom: 0px;
}

.features__link {
  color: #979797;
  font-family: "Oxygen";
  font-size: 13px;
  cursor: pointer;
  text-decoration: underline;
  letter-spacing: 0.03em;
}

.features__link:hover {
  color: #b297c4;
}

.features__title--icon-headphones,
.features__title--icon-refresh,
.features__title--icon-clock,
.features__title--icon-comments {
  position: relative;
}
.features__title--icon-headphones::before,
.features__title--icon-refresh::before,
.features__title--icon-clock::before,
.features__title--icon-comments::before {
  content: "";
  position: absolute;
  top: -4rem;
  left: 50%;
  transform: translateX(-50%);
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: 3.3rem;
  color: #b297c4;
}

.features__title--icon-headphones::before {
  content: "\f025";
  top: -4.6rem;
  font-size: 4rem;
}

.features__title--icon-refresh::before {
  content: "\f021";
  top: -4.2rem;
}

.features__title--icon-clock::before {
  content: "\f017";
  transform: scaleX(-1) translateX(50%);
  top: -4.3rem;
  font-size: 3.4rem;
}

.features__title--icon-comments::before {
  content: "\f0e6";
  top: -4.4rem;
  font-size: 3.4rem;
}

.main-aside-wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  column-gap: 30px;
  margin-top: 56px;
}
@media screen and (max-width: 870px) {
  .main-aside-wrapper {
    justify-content: center;
  }
}

.post-wrapper {
  flex-grow: 1;
  max-width: 870px;
  min-width: 405px;
  max-height: 1500px;
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  column-gap: 30px;
  flex-wrap: wrap;
  align-content: space-evenly;
  overflow: hidden;
}
@media screen and (max-width: 1170px) {
  .post-wrapper {
    max-height: 2000px;
  }
}
@media screen and (max-width: 870px) {
  .post-wrapper {
    max-height: 4000px;
  }
}

.post {
  background-color: #ffffff;
  box-shadow: 0px 1px 4px 0px rgba(82, 84, 77, 0.16);
  flex-shrink: 0;
  border-radius: 6px;
  width: 270px;
  overflow: hidden;
  line-clamp: 6;
  text-overflow: ellipsis;
}

.post__header,
.post__image-wrapper,
.post__text {
  padding-left: 20px;
  padding-right: 20px;
}

.post__image-wrapper--4-img {
  display: flex;
  gap: 18px 20px;
  flex-wrap: wrap;
}
.post__image-wrapper--4-img > .post__img {
  max-width: 115px;
}

.post__header {
  line-height: 1.5rem;
  margin-top: 27px;
  margin-bottom: 24px;
}

.post__img {
  max-width: 230px;
  border-radius: 6px;
}

.post__text {
  font-family: "Oxygen";
  font-weight: 300;
  font-size: 13px;
  line-height: 1.375rem;
  letter-spacing: 0.025rem;
  margin-top: 20px;
  margin-bottom: 25px;
  color: #979797;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  line-clamp: 6;
  -webkit-box-orient: "vertical";
}

.post__link {
  background-color: #b297c4;
  padding: 15px 20px 11px;
  text-decoration: none;
  color: #ffffff;
  font-family: "FjallaOne";
  font-size: 14px;
  line-height: 1.5rem;
  text-transform: uppercase;
  letter-spacing: -0.02em;
  text-align: start;
  display: inline-block;
  width: 100%;
  position: relative;
}
.post__link::after {
  content: "\f061";
  position: absolute;
  top: 50%;
  left: 5.45rem;
  transform: translateY(-50%);
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  color: inherit;
