.features {
  @include box-styling;
}

.features__list {
  @include reset("ul");
  display: flex;
  flex-wrap: wrap;
  min-height: 177px;
}

.features__item {
  flex-basis: 295px;
  flex-grow: 1;
  flex-shrink: 0;
  padding-bottom: 20px;
  text-align: center;
  background: linear-gradient(
    92.5deg,
    rgba(226, 226, 226, 1) 0%,
    rgba(190, 190, 190, 1) 1%,
    rgba(0, 0, 0, 0) 3%
  );

  &--first,
  &--second,
  &--third {
    padding-right: 29px;
  }

  @media screen and (max-width: $max-width) {
    background: inherit;
    
    &--first,
    &--second,
    &--third {
      padding-right: 0;
      border-bottom: 1px solid rgba(190, 190, 190, 0.5);
    }
  }
}

.features__item:first-child {
  background: $bg-block-color;
}

.features__item:last-child {
  flex-basis: 265px;
}

.features__title {
  @extend %h3-text-style;
  margin-top: 100px;
  margin-bottom: 0px;
}

.features__link {
  color: $main-text-color;
  font-family: "Oxygen";
  font-size: 13px;
  cursor: pointer;
  text-decoration: underline;
  letter-spacing: 0.03em;
}

.features__link:hover {
  color: $accent-color;
}

.features__title--icon-headphones,
.features__title--icon-refresh,
.features__title--icon-clock,
.features__title--icon-comments {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    top: -4rem;
    left: 50%;
    transform: translateX(-50%);
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: 3.3rem;
    color: $accent-color;
  }
}

.features__title--icon-headphones::before {
  content: "\f025";
  top: -4.6rem;
  font-size: 4rem;
}

.features__title--icon-refresh::before {
  content: "\f021";
  top: -4.2rem;
}

.features__title--icon-clock::before {
  content: "\f017";
  transform: scaleX(-1) translateX(50%);
  top: -4.3rem;
  font-size: 3.4rem;
}

.features__title--icon-comments::before {
  content: "\f0e6";
  top: -4.4rem;
  font-size: 3.4rem;
}

@media screen and (max-width: $breakpoint-tablet) {
}
