.main-aside-wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  column-gap: $main-flex-gap;
  margin-top: 56px;

  @media screen and (max-width: $post-wrapper-width) {
    justify-content: center;
  }
}

.post-wrapper {
  flex-grow: 1;
  max-width: $post-wrapper-width;
  min-width: $block-width * 1.5;
  max-height: 1500px;
  display: flex;
  flex-direction: column;
  row-gap: $main-flex-gap;
  column-gap: $main-flex-gap;
  flex-wrap: wrap;
  align-content: space-evenly;
  overflow: hidden;

  @media screen and (max-width: $max-width) {
    max-height: 2000px;
  }

  @media screen and (max-width: $post-wrapper-width) {
    max-height: 4000px;
  }
}

.post {
  @include box-styling;
  flex-shrink: 0;
  border-radius: $box-border-radius;
  width: $block-width;
  overflow: hidden;
  line-clamp: 6;
  text-overflow: ellipsis;
}

.post__header,
.post__image-wrapper,
.post__text {
  padding-left: 20px;
  padding-right: 20px;
}

.post__image-wrapper--4-img {
  display: flex;
  gap: 18px 20px;
  flex-wrap: wrap;
  & > .post__img {
    max-width: ($block-width / 2 - 20px);
  }
}

.post__header {
  @extend %h3-text-style;

  line-height: 1.5rem;
  margin-top: 27px;
  margin-bottom: 24px;
}

.post__img {
  max-width: $block-width - 40px;
  border-radius: $box-border-radius;
}

.post__text {
  font-family: "Oxygen";
  font-weight: 300;
  font-size: 13px;
  line-height: 1.375rem;
  letter-spacing: 0.025rem;
  margin-top: 20px;
  margin-bottom: 25px;
  color: $main-text-color;

  @include lineClamp($post-text-line);
}

.post__link {
  background-color: $accent-color;
  padding: 15px 20px 11px;
  text-decoration: none;
  color: $post-link-color;
  font-family: "FjallaOne";
  font-size: 14px;
  line-height: 1.5rem;
  text-transform: uppercase;
  letter-spacing: -0.02em;
  text-align: start;
  display: inline-block;
  width: 100%;
  position: relative;

  &::after {
    content: "\f061";
    position: absolute;
    top: 50%;
    left: 5.45rem;
    transform: translateY(-50%);
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    color: inherit;
  }

  &:hover::after{
    left: 5.6rem;
    transition: left 0.2s;
  }
}
