.search-form {
  position: relative;
  border-radius: $box-border-radius;
  background-color: $white-smoke;

  &.focus {
    background-color: $white;
    outline-style: solid;
    outline-width: 2px;
    outline-color: $accent-color;
  }
}

.search-form__input {
  width: 90%;
  font-family: "FjallaOne";
  font-weight: normal;
  border: none;
  background-color: transparent;
  color: $title-text-color;
  padding: 7px 5px;
  line-height: 18px;
  font-size: 1rem;

  &:focus {
    outline: none;
  }
}

.search-form__submit {
  @include reset("button");
  color: $accent-color;
  font-size: 1.1rem;
  position: absolute;
  top: 0.35rem;
  right: 0.5rem;
}
