*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

html,
body {
  min-height: 100%;
  min-width: $min-width;
  position: relative;
}

body {
  background-color: $white-smoke;
  font-size: 1rem;
  font-family: "OpenSans", monospace;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-wrapper {
  position: relative;
  width: 100%;
  max-width: $max-width;
  margin-left: auto;
  margin-right: auto;
}

.display-flex{
  display: flex;
  flex-wrap: wrap;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

%wood-bg {
  background-color: $bg-color;
  background-image: url(../img/bg-wood-pattern.jpg);
  background-repeat: repeat;
  background-position: left top;
}

%h3-text-style{
  color: $title-text-color;
  text-transform: uppercase;
  font-family: "FjallaOne";
  font-size: 1.0625rem;
  font-weight: normal;
}
