.button{
  @include reset("button");
  color: inherit;
  font-size: 1.125rem;
  text-decoration: none;
  text-transform: uppercase;
  padding: 8.0px 11px 6px 10px;
}

.button--transparent{
  background: transparent;
  border: 3px solid $main-banner-text-color;
  border-radius: 5px;
}

.button:hover {
  text-decoration: none;
  border-color: $accent-color;
}

.button:active {
  color: $white;
  text-decoration: none;
  background-color: $accent-color;
  border-color: $accent-color;
}

.button:focus {
  text-decoration: none;
}
