
.copyright__item {
  max-width: $block-width;
  flex-grow: 1;
}

.copyright__title {
  font-size: 0.8125rem;
  font-family: "OpenSans";
  color: $accent-color;
  font-weight: bold;
  line-height: 1.25rem;
  letter-spacing: 0.002em;

  & > span {
    font-weight: lighter;
  }
}

.copyright__text {
  margin: 0;
  font-size: 0.8125rem;
  font-family: "OpenSans";
  color: $footer-text-color;
  font-weight: light;
  line-height: 1.25rem;
  letter-spacing: 0.002em;
}

.copyright__link{
  color: inherit;
  text-decoration: none;

  &:hover{
    text-decoration: underline;
  }
}