.main-header {
  @extend %wood-bg;
  width: 100%;
  height: 636px;
}

.main-header__tape {
  width: 100%;
  background-color: $bg-color;
  height: 60px;
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.43);
  margin-top: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-nav {
  font-family: "FjallaOne", Courier, monospace;
  font-weight: normal;
}

.main-header__wrapper {
  width: $max-width;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header__logo{
  line-height: 0;
}

@media screen and (max-width: $breakpoint-tablet) {
  .main-header{
    height: 906px;
  }
  .main-header__wrapper {
    flex-direction: column;
  }

  .main-nav {
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .main-header__tape {
    height: auto;
  }
}
