.main-banner {
  @include box-styling;
  box-shadow: none;
  font-family: FjallaOne;
  color: $main-banner-text-color;
  height: 447px;
  border: 20px solid $bg-block-color;
  width: 100%;
  border-bottom: none;
  overflow: hidden;
  position: absolute;
  top: -447px;
  border-radius: $box-border-radius $box-border-radius 0 0;
}

.main-banner__figure {
  @include reset(figure);
}

.main-banner__caption {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: $text-letter-spacing-20;
}

.main-banner__toggle {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 50%;
  transform: translateY(-78%);
}

.main-banner__next > svg,
.main-banner__prev > svg {
  height: 47px;
  width: 32px;
}

.main-banner__next > svg {
  transform: scaleX(-1) translateX(-1px);
}

.main-banner__text {
  width: 75%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 39.7%;
  left: 49.8%;
  transform: translate(-50%);
}

.main-banner__title {
  font-size: 4rem;
  font-weight: normal;
  color: $main-banner-text-color;
  text-transform: uppercase;
  text-align: center;
}

.main-banner__title--icon-user {
  position: relative;
  &::before {
    content: "\f007";
    position: absolute;
    top: -7rem;
    left: 50%;
    transform: translateX(-47%);
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: 6.1rem;
    color: inherit;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: $main-banner-text-color;
  }
}

.main-banner__description {
  font-size: 1.375rem;
  color: $main-banner-text-color;
  letter-spacing: $text-letter-spacing-20;
  text-align: center;
  margin-top: 9px;
  margin-bottom: 17px;
  margin-left: 5px;
}

.main-banner__link {
  margin-left: 6px;
}

@media screen and (max-width: $breakpoint-tablet) {
  .main-banner__title {
    font-size: 2.6rem;
  }
}
