.main-footer {
  @extend %wood-bg;
  width: 100%;
  margin-top: 60px;
  padding: 44px 0 60px;

  @media screen and (max-width: $breakpoint-tablet) {
    padding-right: 20px;
    padding-left: 20px;
  }
}

.main-footer__copyright {
  flex-basis: 100%;
  display: flex;
  color: white;
  justify-content: space-between;
  flex-wrap: wrap;
  overflow: hidden;
  border-bottom: 1px solid $footer-border-color;
  padding-bottom: 29px;
  margin-bottom: 36px;

}

.main-footer__social{
  margin-right: $main-flex-gap;
}

.main-footer__nav {
  min-width: $block-width;
}

.main-footer__social {
  min-width: $block-width;
}
