.list {
  @include reset(ul);
  display: flex;
}

.list__item:not(:last-child)::after {
  content: "::";
  color: $accent-color;
  font-size: 0.875rem;
  font-weight: inherit;
  display: inline-block;
  width: 1rem;
  margin-left: 0.5rem;
}

.list__link {
  font-size: 13px;
  font-family: "Open Sans";
  color: $accent-color;
  font-weight: bold;
  line-height: 28px;
  text-decoration: none;

  &:hover{
    color: $footer-text-color;
    text-decoration: underline;
  }
}