.aside {
  flex-shrink: 0;
  width: $block-width;
  @media screen and (max-width: $post-wrapper-width) {
    margin-top: $main-flex-gap;
  }
}

.aside__search,
.aside__topics,
.aside__reviews {
  @include box-styling;
  border-radius: $box-border-radius;
  display: block;
  padding: 23px 20px 20px;
  margin-bottom: $main-flex-gap;
}

.aside__search__title,
.aside__topics__title,
.aside__reviews__title {
  @extend %h3-text-style;
  margin-top: 0;
  margin-bottom: 0.9rem;
}

.aside__topics__list,
.aside__reviews__list {
  @include reset("ul");
}

.aside__topics__item {
  @include reset("li");
  padding-left: 0px;
  border-bottom: 1px solid $white-smoke;

  &::before {
    content: "\f105";
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: 0.8rem;
    color: $accent-color;
    margin-left: 0rem;
    width: 1em;
  }

  &:last-child {
    border-bottom: none;
  }
}

.aside__topics__item > a {
  text-decoration: none;
  color: $main-text-color;
  font-family: "Oxygen";
  font-weight: 300;
  font-size: 0.8125rem;
  line-height: 2.18rem;
  letter-spacing: 0.04em;

  &:hover {
    color: $accent-color;
    font-weight: 400;
  }

  &:active {
    color: $white-smoke;
  }
}

.aside__reviews__title {
  margin-bottom: 0.45rem;
}

.aside__reviews__item {
  @include reset("li");
  position: relative;
  height: 100px;
  border-bottom: 2px solid $white-smoke;
  display: flex;
  align-items: center;
  gap: 18px;

  &::before {
    content: "\f0f3";
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    background-color: $accent-color;
    border-radius: 4px;
    width: 60px;
    height: 60px;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: 1.5rem;
    color: $white;
  }

  &:last-child {
    border-bottom: none;
  }

  &--notification {
    &::before {
      content: "\f0f3";
    }
  }

  &--meeting {
    &::before {
      content: "\f0fc";
    }
  }

  &--quote {
    &::before {
      content: "\f10e";
    }
  }
}

.aside__reviews-text-wrapper {
  height: 70px;
}

.aside__reviews__author {
  font-size: 13px;
  font-family: "FjallaOne";
  margin: 0 0 5px 0;
  color: $accent-color;
  text-transform: uppercase;
  letter-spacing: 0.02em;
}

.aside__reviews__link {
  @include lineClamp(2);
  font-size: 13px;
  font-family: "Oxygen";
  color: $main-text-color;
  line-height: 1.375rem;

  &:hover {
    color: $accent-color;
  }
}
