@mixin font-face($name, $file, $weight: 400, $style: normal) {
  @font-face {
    font-family: "#{$name}";
    src: local("#{$file}"), url("../fonts/#{$file}.woff2") format("woff2"),
      url("../fonts/#{$file}.woff") format("woff");
    font-weight: $weight;
    font-style: $style;
    font-display: swap;
  }
}

@mixin box-styling {
  background-color: $bg-block-color;
  box-shadow: 0px 1px 4px 0px $box-shadow-color;
}

@mixin reset($type) {
  $resets: (
    ul: (
      list-style: none,
      margin: 0,
      padding: 0,
    ),
    li: (
      margin: 0,
      padding: 0,
    ),
    figure: (
      margin: 0,
    ),
    button: (
      padding: 0,
      border: 0,
      background-color: transparent,
      border-radius: 0,
      cursor: pointer,
      appearance: none,
      display: "inline-block",
    ),
  );
  @each $prop, $value in map-get($resets, $type) {
    @if type-of($value) == "map" {
      & > #{$prop} {
        @each $sub-prop, $sub-value in $value {
          #{$sub-prop}: $sub-value;
        }
      }
    } @else {
      #{$prop}: $value;
    }
  }
}

@mixin lineClamp($lineClamp: 1, $boxOrient: "vertical") {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: $lineClamp;
  line-clamp: $lineClamp;
  -webkit-box-orient: $boxOrient;
}
